.slider {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100%;
  overflow: hidden;
}

.carouselImage-container {
  display: flex;
  transform: translateX(0);
  transition: transform 0.8s ease-in-out;
  width: 100%;
  height: 100%;
  max-height: 100%;
  .image {
    width: 100%;
    height: 100%;
  }
}
.slide {
  transform: translateX(5000px);
}

.slideActive {
  transform: translateX(0px);
  transition: transform 1s ease-in-out;
}
.right-arrow,
.left-arrow {
  position: absolute;
  font-size: 20px;
  color: #696969;
  cursor: pointer;
  user-select: none;
  font-weight: bold;
  top: 50%;
  transform: translate(0, -50%);
}

.right-arrow {
  right: 5px;
}

.left-arrow {
  left: 5px;
}
